import React from "react";
import { Header, Rightbar } from "../components";
import { useSelector } from "react-redux";
import { ToastContainer, toast } from 'react-toastify';

const Home = ({history}) => {
  const {ws,token,userinfo} = useSelector((state) => state)
  const logs = [
    {
      txt: "Active Cup 1 and Fill it to PH7",
      date: "24/06/1999",
      time: "06:01:02",
    },
    {
      txt: "Active Cup 1 and Fill it to PH7",
      date: "24/06/1999",
      time: "06:01:02",
    },
    {
      txt: "Active Cup 1 and Fill it to PH7",
      date: "24/06/1999",
      time: "06:01:02",
    },
  ];

  const sendcommand = message => {
    console.log('user',message)
    console.log(ws.readyState)
    try {
      if(ws.readyState == 1){
        ws.send(
          JSON.stringify({ 
            type: "mqttclientcommand",
            message,
            id : `${userinfo._id}`
          })
         ) 
         toast(`${Object.keys(message)[0]} Command sended`,{
          theme : 'light'
          });
      }
      else {
        toast(`Make sure your connection is established`,{
          theme : 'dark'
          });
      }
       
    } catch (error) {
      console.log(error)
    }
    
 };  

  return (
    <>
      <Header title="Dashboard" history={history}/>
      <div className="home-p flex rel sidebar-gap">
      <ToastContainer />
        <div className="container flex flex-col aic">
          <div className="cirlce-btns">
            <button   onClick={() => sendcommand({EC2 : 'start'})} className="btn cleanbtn flex aic font s16 c000 b3">
              EC2
            </button>
            <button   onClick={() => sendcommand({NO210 : 'start'})} className="btn cleanbtn flex aic font s16 c000 b3">
              No2 (10)
            </button>
            <button   onClick={() => sendcommand({NO2100 : 'start'})} className="btn cleanbtn flex aic font s16 c000 b3">
              No2 (100)
            </button>
            <button className="btn cleanbtn flex aic font s16 c000 b3"></button>
            <button 
              onClick={() => sendcommand({Imacimus : 'start'})}
            className="btn cleanbtn flex aic font s16 ">
              <div className="txt font s12 c000 b3">Imacimus conditioning</div>
            </button>
            <button
             onClick={() => sendcommand({PH4 : 'start'})}
            className="btn cleanbtn flex aic font s16 c000 b3">
              PH4
            </button>
            <button
            onClick={() => sendcommand({PH7 : 'start'})}
            className="btn cleanbtn flex aic font s16 c000 b3">
              PH7
            </button>
            <button
            onClick={() => sendcommand({KCL : 'start'})}
            className="btn cleanbtn flex aic font s16 c000 b3">
              KCL
            </button>
            <button 
            onClick={() => sendcommand({MION1 : 'start'})}
            className="btn cleanbtn flex aic font s16 c000 b3">
              M-ION ( P1 )
            </button>
            <button
            onClick={() => sendcommand({Distilledwater : 'start'})}
            className="btn cleanbtn flex aic font s16 c000 b3">
              Distilled water
            </button>
            <button
            onClick={() => sendcommand({MION2 : 'start'})}
            className="btn cleanbtn flex aic font s16 c000 b3">
              M-ION ( P2 )
            </button>
            <button
            onClick={() => sendcommand({EC1 : 'start'})}
            className="btn cleanbtn flex aic font s16 c000 b3">
              EC1
            </button>
            <button className="btn cleanbtn flex aic font s16 c000 b3">
              <div className="inline-cirlce">
                <div
                onClick={() => sendcommand({DO : 'start'})}
                className="inline-cirlce-btn">
                  <div className="text font s15 ">DO</div>
                </div>
                <div 
                onClick={() => sendcommand({AIR : 'start'})}
                className="inline-cirlce-btn">
                  <div className="text font s15 ">AIR</div>
                </div>
                <div 
                onClick={() => sendcommand({EC : 'start'})}
                className="inline-cirlce-btn">
                  <div className="text font s15 ">EC</div>
                </div>
                <div 
                onClick={() => sendcommand({PH : 'start'})}
                className="inline-cirlce-btn">
                  <div className="text font s15 ">PH</div>
                </div>
                <div 
                onClick={() => sendcommand({MI : 'start'})}
                className="inline-cirlce-btn">
                  <div className="text font s15 ">MI</div>
                </div>
              </div>
            </button>
          </div>
          <div className="square-btns flex aic">
            <button
            onClick={() => sendcommand({wasterdrawer : 'start'})}
            className="btn cleanbtn flex aic font s16 c000 b3">
              <div className="item">Waste drawer</div>
            </button>
            <button 
            onClick={() => sendcommand({samplewastedrawer : 'start'})}
            className="btn cleanbtn flex aic font s16 c000 b3">
              <div className="item">Sample Waste drawer</div>
            </button>
          </div>
          <div className="calibration-btns flex flex-col">
            <div className="row flex aic">
              <button 
              onClick={() => sendcommand({calibration : 'start'})}
              className="cleanbtn btn font">Calibration</button>
              <button 
              onClick={() => sendcommand({wateranalysis : 'start'})}
              className="cleanbtn btn font">Water Analysis</button>
            </div>
          </div>
        </div>
        <div className="logs-container">
          <Rightbar header={true} title="Activity Log">
            <div className="wrap flex flex-col">
              {logs.map((item) => (
                <div className="item flex aic">
                  <div className="flex aic">
                    <div className="ico color s16 b7">&#8212;</div>
                    <div className="txt font s13 c000">{item.txt}</div>
                  </div>
                  <div className="meta flex flex-col s10 aic">
                    <div className="date font c000 b5">{item.date}</div>
                    <div className="font c000">{item.time}</div>
                  </div>
                </div>
              ))}
            </div>
          </Rightbar>
        </div>
      </div>
    </>
  );
};

export default Home;
