import React, { useState, useEffect } from "react";
import { Header } from "../components";
import { SearchIcon, PlusIcon, ArrowDownIcon } from "../svg";
import axios from 'axios';
import setheadertoken from '../actions/setHeaderToken'
import {useSelector} from 'react-redux'
import Loader from "../components/Loader";

const Users = ({history}) => {
  const {ws,token,userinfo} = useSelector((state) => state)
  const [users,setusers] = useState([])
  const [search,setsearch] = useState('')
  const [loaded,setloaded] = useState(false)
  const loadallusers = async () => {
    if(token){
      try {
        setheadertoken(token)
        const users = await axios.get(`${process.env.REACT_APP_END_URL}/api/getallusers`);
        console.log(users)
        setusers(users.data.users)
        setloaded(true)
      } catch (error) {
        console.log(error.response) 
      }
    }
  }
  useEffect(() => {
    loadallusers()
  },[token])
  // const [users, setUsers] = useState([
  //   {
  //     name: "Frankistin Labar",
  //     email: "Frankistin1122@gmail.com",
  //     roles: [{ label: "Admin" }, { label: "User" }, { label: "Mode" }],
  //   },
  //   {
  //     name: "Frankistin",
  //     email: "Frankis@gmail.com",
  //     roles: [{ label: "Admin" }, { label: "User" }, { label: "Mode" }],
  //   },
  // ]);

  const SearchUsers = async (txt) => {
    try {
      setheadertoken(token)
      const users = await axios.post(`${process.env.REACT_APP_END_URL}/api/searchusers`,{
        search : txt
      });
      console.log(users)
      setusers(users.data.users)
      setloaded(true)
    } catch (error) {
      console.log(error.response) 
    }
  }

  const UserInfo = ({ item }) => {
    const [dropRole, setDropRole] = useState(false);
    const [selectedRole, setSelectedRole] = useState("Admin");
    const [roles, setRoles] = useState([
      { label: "admin" },
      { label: "user" },
     /// { label: "Mode" },
    ]);

    useEffect(() => {
      document.body.addEventListener("click", () => setDropRole(false));
    }, []);

    return (
      <div className="row flex aic">
        <div className="col flex aic">
          <div className="lbl font s15 wordwrap">{item.Username}</div>
        </div>
        <div className="col flex aic">
          <div className="lbl font s15 wordwrap">{item.Email}</div>
        </div>
        <div className="col flex aic">
          {/* Role Button */}
          <button
            className="cleanbtn flex aic font s14 c000 cstm-slt rel"
            onClick={(e) => {
              e.stopPropagation();
              setDropRole(!dropRole);
            }}
          >
            {roles.map(
              (item2) => {
                if( item.usertype == item2.label){
                  console.log('matched')
                  return <div className="lb wordwrap">{item.usertype}</div>
                }
              }
                // item.usertype == item2.label && (
                //   <div className="lb wordwrap">{item.label}</div>
                // )
            )}
            {dropRole && (
              <div className="options flex flex-col abs">
                {roles.map((item) => (
                  <button
                    className="cleanbtn item flex aic anim"
                    onClick={() => {
                      setDropRole(!dropRole);
                      setSelectedRole(item.label);
                    }}
                  >
                    <div className="txt font s14 c000 wordwrap">
                      {item.label}
                    </div>
                  </button>
                ))}
              </div>
            )}
            <div className="ico s12 anim">
              <ArrowDownIcon />
            </div>
          </button>
        </div>
        <div className="col flex aic">
          <button
            className="cleanbtn cross-btn font cfff flex aic"
            onClick={() => console.log("Deleted")}
          >
            &times;
          </button>
        </div>
      </div>
    );
  };

  return (
    <>
      <Header title="User Managment" hideRightbar={true} history={history} />
      <div className="users-p sidebar-gap">
      {
        loaded ?
        <>
        <div className="title-blk flex aic">
          <div className="lit flex aic">
            <div className="title font s20 b6 c000 upc">Users</div>
          </div>
          <div className="rit flex aix">
            <button className="cleanbtn add-btn flex aic">
              <div className="ico">
                <PlusIcon />
              </div>
              <div className="txt s15 b5 cfff">Add User</div>
            </button>
          </div>
        </div>
        <div className="srch-blk flex aic">
          <div className="srch-bar flex aic">
            <input 
            value={search}
            onChange={(e) => {
              setsearch(e.target.value)
              SearchUsers(e.target.value)
             }
            }
            type="text" className="iput cleanbtn font s14 c000" />
            <button className="cleanbtn icon">
              <SearchIcon />
            </button>
          </div>
        </div>

        {/* Table */}
        <div className="table flex flex-col">
          <div className="table-hdr flex aic">
            <div className="col flex aic">
              <div className="lbl font s15">Name</div>
            </div>
            <div className="col flex aic">
              <div className="lbl font s15">E-mail</div>
            </div>
            <div className="col flex aic">
              <div className="lbl font s15">Role</div>
            </div>
            <div className="col flex aic">
              <div className="lbl font s15">Action</div>
            </div>
          </div>
          {users.map((item, index) => (
            <UserInfo item={item} />
          ))}
        </div>
        </>
        :
        <Loader />
      }
      </div>
      ;
    </>
  );
};

export default Users;
