import setheadertoken from './setHeaderToken'
export const loaduser = () => async (dispatch) => {
    console.log('loaduser')
    
    try {
        const value = await localStorage.getItem('userinfo')
        if(value !== null) {
         console.log('got from async storage',value)
         setheadertoken(JSON.parse(value).token);
         dispatch({type : 'LOGIN_SUCCESS', payload : JSON.parse(value)})
        }
      } catch(e) {
        console.log(e)
      }
    
  };