import React, { useState, useEffect,useRef } from "react";
import { Header, Rightbar } from "../components";
import {
  MessengerUserIcon,
  MessengerSendIcon,
  MessengerFileIcon,
} from "../svg";
import Messanger from "./Messanger";
import {useSelector} from 'react-redux'
import axios from 'axios'
import setheadertoken from '../actions/setHeaderToken'
import moment from "moment";
import Loader from "../components/Loader";

const Users = ({history}) => {
  const {ws,token,userinfo} = useSelector((state) => state)
  const [input, setInput] = useState("");
  const [loaded,setloaded] = useState(false)
  const [messagesloaded,setmessagesloaded] = useState(false)
  const [messages, setMessages] = useState([]);
  const [chats,setchats] = useState([])
  const [selecteduserid,setselecteduserid] = useState()
  const dummy = useRef();
  const [wsmessage,setwsmessage] = useState('')

  const sendMessages = (message) => {
      console.log('messages',{
        type: "chat",
        message : message,
        id :  userinfo.usertype == "user" ? `6162ecc4e6a2fe5cc80253e7` : selecteduserid
      })
      ws.send(
        JSON.stringify({
          type: "chat",
          message : message,
          id :  userinfo.usertype == "user" ? `6162ecc4e6a2fe5cc80253e7` : selecteduserid
        })
      )
    setMessages([
      ...messages,
      { user :  `${userinfo._id}`, message, date : new Date() },
    ]);
    setInput("");
    dummy.current.scrollIntoView({
      behavior: "smooth",
      block: "nearest",
      inline: "center",
      alignToTop: false,
    });
  };

  const getusermessagesbyid = async (id) => {
    try {
      setheadertoken(token)
      const chats = await axios.post(`${process.env.REACT_APP_END_URL}/api/getUserMessageById`,{id});
      console.log(chats.data)
      console.log('getmessages called')
      if(chats.data.chat === null){
        setMessages([])
      }else {
      setMessages(chats.data.chat.messages) 
      }   
      setloaded(true)
    } catch (error) {
      console.log(error.response) 
    }
  }
console.log(messages)
  const loadalluserschat = async () => {
    if(token){
      try {
        setheadertoken(token)
        const chats = await axios.get(`${process.env.REACT_APP_END_URL}/api/getAllChat`);
        setchats(chats.data.chat)
        setloaded(true)
      } catch (error) {
        console.log(error.response) 
      }
    }
  }
  useEffect(() => {
    loadalluserschat()
  },[token])

  useEffect(() => {
    const listensocket = () => {
      if(ws){
        ws.addEventListener('message', function (mydata) {
        const thedata = (JSON.parse(mydata.data)) 
        console.log('got the message',thedata)
        console.log(selecteduserid)
        if(thedata.type == 'chat'){
          if(selecteduserid == thedata.id){
            setwsmessage({ user :  `${thedata.id}`, message : thedata.message, date : new Date() })
          } 
        }
        }) 
      }
    }
    listensocket() 
},[ws,selecteduserid]) 
useEffect(() => {
  setMessages([
    ...messages,
    wsmessage
  ]);
},[wsmessage])

  return (
    <>
      <Header title="Support Managment" rightbarIcon="support" history={history}/>
      {
      loaded ?
      <div className="support-managment-p flex sidebar-gap">
        <div className="container flex flex-col">
          <div className="page-hdr flex">
            <div className="title font s20 b6 c000 upc">Clients</div>
          </div>

          {/* Table */}
          <div className="table flex flex-col">
            <div className="table-hdr flex aic">
              <div className="col flex aic">
                <div className="lbl font s15">Name</div>
              </div>
              <div className="col flex aic">
                <div className="lbl font s15">Email</div>
              </div>
              <div className="col flex aic">
                <div className="lbl font s15">Date</div>
              </div>
              <div className="col flex aic">
                <div className="lbl font s15">Subject</div>
              </div>
              <div className="col flex aic">
                <div className="lbl font s15">Action</div>
              </div>
            </div>
            {chats.map((item, index) => (
              <div key={index} className="row flex aic">
                <div className="col flex aic">
                  <div className="lbl font s15">{item.senderid?.Username}</div>
                </div>
                <div className="col flex aic">
                  <div className="lbl font s15">{item.senderid?.Email}</div>
                </div>
                <div className="col flex aic">
                  <div className="lbl font s15">{moment(item.messages[0]?.created).calendar()}</div>
                </div>
                <div className="col flex aic">
                  <div className="lbl font s15">{item.title}-{item.subject}</div>
                </div>
                <div className="col flex aic">
                  <button onClick={() => {
                      getusermessagesbyid(item.senderid._id)
                      setselecteduserid(item.senderid._id)
                      }
                    }>View messages</button>
                </div>
              </div>
            ))}
          </div>
        </div>
        <div className="manag-rightbar">
          <Rightbar header={true} name="messanger">
            <div className="meta flex flex-col aic">
              <div className="dp flex aic">
                <MessengerUserIcon />
              </div>
              <div className="nam font s16 b6 upc c68">Frankistian</div>
            </div>
            <Messanger dummy={dummy} sendMessages={sendMessages} messages={messages} input={input} setInput={setInput} />
          </Rightbar>
        </div>
      </div>
      :
      <Loader />
      }
    </>
  );
};

export default Users;
