import React, { useEffect } from "react";
import "./css/App.scss";
import axios from "axios";
import 'react-toastify/dist/ReactToastify.css';
import "react-datepicker/dist/react-datepicker.css";

// Screens
import { BrowserRouter, Switch, Route,Redirect } from "react-router-dom";
import Siderbar from "./components/Sidebar";
import Home from "./screens/Home";
import Users from "./screens/Users";
import Statistics from "./screens/Statistics";
import Support from "./screens/Support";
import SupportManagment from "./screens/SupportManagment";
import Companies from "./screens/Companies";
import Download from "./screens/Download";
import Login from './screens/Login'
//import ForgetPassword from './screens/ForgotPassword'

// redux
import {loaduser} from './actions/auth'
import {useDispatch,useSelector} from 'react-redux'

function App() {
  const dispatch = useDispatch()
  const {isAuthenticated,token} = useSelector(state => state)
  const state = useSelector(state => state)
  const {userinfo} = useSelector(state => state)
  console.log('state',useSelector(state => state))
 
  useEffect(() => {
    const checkuser = async () => {
      const value = await localStorage.getItem('userinfo')
      if(value){
        await dispatch(loaduser())
      }
  }
    checkuser()
    
  },[])
  useEffect(() => {
  if(isAuthenticated){
    const connectwithsocket = () => {
      let ws = new WebSocket(`${process.env.REACT_APP_WS_URL}/?token=${token}`);
       ws.onopen = () => dispatch({type : 'UPDATE_WS', payload : ws})  
        ws.onclose = function(){
          console.log('disconneted')
          setTimeout(function(){connectwithsocket()}, 5000);
        };  
        ws.addEventListener('message', function (mydata) {
          const thedata = (JSON.parse(mydata.data)) 
          console.log('maineventlistener',thedata)
          if(thedata.type == 'mqtt'){
            dispatch({type : 'UPDATE_DEVICES_INFO', payload : thedata.message})  
          }
          })
      }
      connectwithsocket()
  }
 
  },[isAuthenticated])
  return (
    <div className="App rel">
      <BrowserRouter>
      {
        !isAuthenticated ? 
        <>
          <Redirect to={`${window.location.pathname === '/' ? "/login" : window.location.pathname }`}/>
          <Route exact path="/login" component={Login} />
        </>
        :
        <>
        <Redirect to={`${window.location.pathname === '/login' ? "/" : window.location.pathname }`}/>
        <Siderbar />
        <Route exact path="/" component={Home} />
        <Route exact path="/users" component={Users} />
        <Route exact path="/statistics" component={Statistics} />
        {userinfo.usertype == 'user' && <Route exact path="/support" component={Support} /> }
        {userinfo.usertype == 'admin' && <Route exact path="/support-managment" component={SupportManagment} /> }
        {/* <Route exact path="/companies" component={Companies} /> */}
        <Route exact path="/download" component={Download} />
        </>
      }
       
      </BrowserRouter>
    </div>
  );
}

export default App;
