import React, { useState,useEffect,useRef } from "react";
import { Header, Rightbar } from "../components";
import { EnvalopIcon, PhoneNumberIcon, EmailIcon } from "../svg";
import Messanger from "./Messanger";
import {useSelector} from 'react-redux'
import axios from 'axios'
import setheadertoken from '../actions/setHeaderToken'
import { ToastContainer, toast } from 'react-toastify';

const Support = ({history}) => {
  const {ws,token,userinfo} = useSelector((state) => state)
  const [input, setInput] = useState("");
  const [username, setUsername] = useState("Mubashir");
  const [title, setTital] = useState("");
  const [subject, setSubject] = useState("");
  const [txt, setTxt] = useState("");
  const [loaded,setloaded] = useState(false)
  const [messages, setMessages] = useState([]);
  const [wsmessage,setwsmessage] = useState('')
  const dummy = useRef();

  const sendfirstMessage = (message) => {
      ws.send(
        JSON.stringify({
          type: "chat",
          message,
          info : {
              title,subject
          },
          id : `6162ecc4e6a2fe5cc80253e7`
      })
      ) 
    setMessages([
      ...messages,
      { user :  `${userinfo._id}`, message,info : {title,subject}, date : new Date() },
    ]);
    setTital("")
    setSubject("")
    setInput("");
  };

  const sendMessages = (message) => {
    if(title || subject) {
      ws.send(
        JSON.stringify({
          type: "chat",
          message,
          info : {
              title,subject
          },
          id : `6162ecc4e6a2fe5cc80253e7`
      })
      ) 
    }
    else {
      console.log('messages',{
        type: "chat",
        message : message,
        id :  `6162ecc4e6a2fe5cc80253e7`
      })
      if(ws.readyState == 1){
      ws.send(
        JSON.stringify({
          type: "chat",
          message : message,
          id :  `6162ecc4e6a2fe5cc80253e7`
        })
      )
      }else {
        toast(`Make sure your connection is established`,{
          theme : 'dark'
          });
          return
      }
    } 
    setMessages([
      ...messages,
      { user :  `${userinfo._id}`, message,info : {title,subject}, date : new Date() },
    ]);
    setTital("")
    setSubject("")
    setInput("");
    dummy.current.scrollIntoView({
      behavior: "smooth",
      block: "nearest",
      inline: "center",
      alignToTop: false,
    });
  };
  console.log('messages',messages)
  useEffect(() => {
    const listensocket = () => {
      if(ws){
        ws.addEventListener('message', function (mydata) {
        const thedata = (JSON.parse(mydata.data)) 
        console.log('got the message',thedata)
        if(thedata.type == 'chat'){
          setwsmessage({ user :  `${thedata.id}`, message : thedata.message, date : new Date() })
        }
        }) 
      }
    }
    listensocket() 
},[ws]) 
useEffect(() => {
  setMessages([
    ...messages,
    wsmessage
  ]);
},[wsmessage])
console.log('input',input)
const getMessages = async () => {
  if(token){
    try {
      setheadertoken(token)
      const chat = await axios.get(`${process.env.REACT_APP_END_URL}/api/getusermessages`);
      console.log(chat.data)
      console.log('getmessages called')
      if(chat.data.chat === null){
        setMessages([])
      }else {
      setMessages(chat.data.chat.messages) 
      }   
      setloaded(true)
    } catch (error) {
      console.log(error.response) 
    }
  }
}
useEffect(() => {
  getMessages()
},[token])

  const _supports = [
    {
      label: "Send support form",
      icon: <EnvalopIcon />,
      address: "",
    },
    {
      label: "Phone numbers",
      icon: <PhoneNumberIcon />,
      address: {
        contact_1: "+92321251064",
        contact_2: "+923212510678",
      },
    },
    {
      label: "Email",
      icon: <EmailIcon />,
      address: { contact_1: "colvito@gmail.com" },
    },
  ];

  return (
    <>
      <Header title="Support" rightbarIcon="support" history={history} />
      <ToastContainer />
      <div className="support-p flex rel sidebar-gap">
        <div className="container flex flex-col">
          {/* Summary block */}
          <div className="summary flex aic">
            {_supports.map((item, index) => (
              <div key={index} className="blk flex flex-col">
                <div className="hdr flex">
                  <div className="lbl font s16 c333 upc">{item.label}</div>
                  <div className="ico flex aic">{item.icon}</div>
                </div>
                <div className="meta flex flex-col">
                  <div className="txt font s12">{item.address.contact_1}</div>
                  <div className="txt font s12">{item.address.contact_2}</div>
                </div>
              </div>
            ))}
          </div>
        </div>
        {
        loaded ? 
        messages.length > 0 ?
          <div className="manag-rightbar">
            <Rightbar header={true} name="messanger">
              <div className="meta flex flex-col aic">
                <div className="dp flex aic">
                  {/* <MessengerUserIcon /> */}
                </div>
                <div className="nam font s16 b6 upc c68">Frankistian</div>
              </div>
              <Messanger dummy={dummy} sendMessages={sendMessages} messages={messages} input={input} setInput={setInput} username={username} setUsername={setUsername}/>
            </Rightbar>
          </div>
        :
          <div className="support-rightbar">
          <Rightbar header={true}>
              <div className="support-form flex flex-col aic">
                <div className="tit font s17 b6 c68 upc">Support Form</div>
                <div className="feild flex flex-col aic">
                  <div className="lbl font s14 c68">Tital Of Content</div>
                  <input
                    className="cleanbtn iput font s14 c68"
                    type="text"
                    onChange={(e) => setTital(e.target.value)}
                  />
                </div>
                <div className="feild flex flex-col aic">
                  <div className="lbl font s14 c68">Subject</div>
                  <input
                    className="cleanbtn iput font s14 c68"
                    type="text"
                    onChange={(e) => setSubject(e.target.value)}
                  />
                </div>
                <div className="feild flex flex-col aic">
                  <div className="lbl font s14 c68">Insert Text</div>
                  <textarea
                    className="cleanbtn iput area font s14 c68"
                    type="text"
                    onChange={(e) => setInput(e.target.value)}
                  />
                </div>
                <button
                onClick={() => {
                  sendfirstMessage(input)
                }}
                className="submit cleanbtn s13 b6 upc cfff">Send</button>
              </div>
            </Rightbar> 
          </div>
        :
        <div>Loading</div>
        }
      </div>
    </>
  );
};

export default Support;
