import React,{useState} from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import {useDispatch} from 'react-redux'
import { ToastContainer, toast } from 'react-toastify';

const Login = () => {
  const [email,setemail] = useState('')
  const [password,setpassword] = useState('')
  const dispatch = useDispatch()
  const login = async () => {
        if(!email || !password){
          toast("Please fill all input",{
            theme : 'dark'
          });
        }
        try {
          const res = await axios.post(`${process.env.REACT_APP_END_URL}/api/signin`,{
            email,password
        }); 
          console.log(res.data)
          localStorage.setItem( 
            'userinfo',
            JSON.stringify(res.data)
          ); 
          dispatch({type : 'LOGIN_SUCCESS', payload : res.data})
        } catch (error) {
          console.log(error.response) 
          if(error.response.data) {
              toast(`${error.response.data.error}`,{
                theme : 'dark'
              });
          }
        }
  }
  return (
    <div className="login-p flex flex-col aic">
      <div className="wrapper">
      <ToastContainer />
        <div className="logo flex aic">
          <img src="./images/logo.svg" className="img" />
        </div>
        <div className="title font s32 b5 c000">Login</div>
        <div className="feild flex flex-col">
          <div className="lbl font s14 c68">Email</div>
          <input 
          value={email}
          onChange={(e) => setemail(e.target.value)}
          type="text" className="iput font s14 c000" />
        </div>
        <div className="feild flex flex-col">
          <div className="lbl font s14 c68">Password</div>
          <input 
           value={password}
           onChange={(e) => setpassword(e.target.value)}
          type="password" className="iput font s14 c000" />
        </div>
        <div className="feild flex flex-col">
          <button 
          onClick={() => login()}
          className="cleanbtn submit-btn font s15 cfff">Login</button>
        </div>
        {/* <Link to="/forgot" className="font s14 c68">
          Forgot Password ?
        </Link> */}
      </div>
    </div>
  );
};

export default Login;
