import React, { useEffect, useState } from "react";
import { Header, Rightbar } from "../components";
import {
  UptimeIcon,
  TemperatureIcon,
  DropIcon,
  OutlineCalenderIcon,
  FanIcon,
  SolutionLevelIcon,
  PumpStatusIcon,
  WaterLevelIcon,
  ActivityStatusIcon,
  SmallLoader
} from "../svg";
import {useSelector} from 'react-redux'
import DatePicker from "react-datepicker";
import axios from "axios";
import moment from "moment";

const Statistics = ({history}) => {
  const [activeTab, setActiveTab] = useState({ label: "Calibrations", value : 'calibration' });
  const [fan, setFan] = useState(true);
  const [startDate, setStartDate] = useState(new Date());
  const {devicesinfo} = useSelector(state => state)
  const [activities,setactivities] = useState([])
  const {fans,samplewaterlevel,wastelevel,wasteactivitystatus,
    drawerwaterlevel,draweractivitystatus,cupswaterlevel,cupactivitystatus,containerlevel,pumpworkstatus} = devicesinfo

  useEffect(() => {
    if(fans == "on"){
      setFan(true)
    }
    else {
      setFan(false)
    }
    },[fans])
    console.log('activeTab',activeTab)
    const getactivities = async () => {
      console.log('activities')
      try {
        const res = await axios.post(`${process.env.REACT_APP_END_URL}/api/getactivitiesbydate`,{
          startDate,
          activity :activeTab.value
        }); 
        console.log(res.data)
        setactivities(res.data.activities)
      } catch (error) {
        console.log(error.response) 
      }
    }
    useEffect(() => {
      getactivities()
    },[startDate,activeTab])

  const [activityTabs, setActivityTabs] = useState([
    { label: "Calibrations", value : 'calibration' },
    { label: "Water Analysis",value : 'wateranalysis' },
    // { label: "Support Requests",value : 'calibration' },
  ]);

  const waterSensing = [
    {
      date: "24/06/1999",
      time: "12:01:12",
      duration: "12mins 22sec",
      excuted_by: "Frankistin",
    },
    {
      date: "24/06/1999",
      time: "12:01:12",
      duration: "12mins 22sec",
      excuted_by: "Frankistin",
    },
    {
      date: "24/06/1999",
      time: "12:01:12",
      duration: "12mins 22sec",
      excuted_by: "Frankistin",
    },
    {
      date: "24/06/1999",
      time: "12:01:12",
      duration: "12mins 22sec",
      excuted_by: "Frankistin",
    },
    {
      date: "24/06/1999",
      time: "12:01:12",
      duration: "12mins 22sec",
      excuted_by: "Frankistin",
    },
    {
      date: "24/06/1999",
      time: "12:01:12",
      duration: "12mins 22sec",
      excuted_by: "Frankistin",
    },
    {
      date: "24/06/1999",
      time: "12:01:12",
      duration: "12mins 22sec",
      excuted_by: "Frankistin",
    },
  ];

  return (
    <>
      <Header title="Statistics" rightbarIcon="setting" history={history}/>
      <div className="statistic-p flex rel sidebar-gap">
        <div className="container flex flex-col">
          {/* Summary block */}
          <div className="summary flex aic">
              <div className="blk flex flex-col">
                <div className="hdr flex aic">
                  <div className="lbl font s16 b3 c333 upc">Uptime</div>
                  <div className="ico flex aic"><UptimeIcon /></div>
                </div>
                <div className="meta rel">
                  <div className="txt font s26 b6">"24:01:00"</div>
                </div>
              </div>
              <div className="blk flex flex-col">
                <div className="hdr flex aic">
                  <div className="lbl font s16 b3 c333 upc">Temperature</div>
                  <div className="ico flex aic"><TemperatureIcon /></div>
                </div>
                <div className="meta rel">
                  <div className="txt font s26 b6">{`${devicesinfo.temp ? `${devicesinfo.temp}C` : 'Loading'}`}</div>
                  <div className="symbol abs" />
                </div>
              </div>
              <div  className="blk flex flex-col">
                <div className="hdr flex aic">
                  <div className="lbl font s16 b3 c333 upc">humidity</div>
                  <div className="ico flex aic"><DropIcon /></div>
                </div>
                <div className="meta rel">
                  <div className="txt font s26 b6">{`${devicesinfo.humidity ? `${devicesinfo.humidity}%` : 'Loading'}`}</div>
                </div>
              </div>
          </div>
            
          {/* Activity Meter */}
          <div className="activity-meter flex aic">
            <div className="left flex aic">
              <div className="activity-tit font s18 b6 upc c68">
                Activity Meter
              </div>
            </div>
            <div className="blk flex aic">
              <div className="center flex aic">
                <div className="actions flex aic">
                  {activityTabs.map((item, index) => (
                    <button
                      className={`cleanbtn btn font s14 b5 c68 upc anim ${
                        activeTab.label === item.label ? "active" : ""
                      }`}
                      onClick={() => setActiveTab({label : item.label, value : item.value })}
                    >
                      {item.label}
                    </button>
                  ))}
                </div>
              </div>
              <div className="right flex aic">
                <button className="cleanbtn calender flex aic">
                  <div className="ico">
                    <OutlineCalenderIcon />
                  </div>
                  <div className="txt font s13 b5 c68"> <DatePicker selected={startDate} onChange={(date) => setStartDate(date)} /></div>
                </button>
              </div>
            </div>
          </div>

          <div className="activity-content flex flex-col rel">
            <div className="hdr">
              <div className="meta flex aic">
                <div className="tit font s18 b6 c68 upc flex aic">
                  Water Sensing
                </div>
              </div>
              <div className="row flex aic">
                <div className="col flex aic">
                  <div className="lbl font wordwrap">Date</div>
                </div>
                <div className="col flex aic">
                  <div className="lbl font wordwrap">Time</div>
                </div>
                <div className="col flex aic">
                  <div className="lbl font wordwrap">Duration</div>
                </div>
                <div className="col flex aic">
                  <div className="lbl font wordwrap">Executed By</div>
                </div>
              </div>
            </div>
            <div className="wrap">
              {activities.map((item, index) => (
                <div className="row flex aic">
                  <div className="col flex aic">
                    <div className="lbl font wordwrap">{moment(item.created).format("MMM Do YY")}</div>
                  </div>
                  <div className="col flex aic">
                    <div className="lbl font wordwrap">{moment(item.created).format("h:mm:ss a")}</div>
                  </div>
                  <div className="col flex aic">
                    <div className="lbl font wordwrap">{item.duration}</div>
                  </div>
                  <div className="col flex aic">
                    <div className="lbl font wordwrap">{item.user.Username}</div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="static-rightbar">
          <Rightbar header={true}>
            <div className="controls flex flex-col">
              {/* Fan Control */}
              <div className="block">
                <div className="tit font s16 c68 upc">Fan Control</div>
                <div className="row flex aic">
                  <div className="meta flex aic">
                    <div className="ico">
                      <FanIcon />
                    </div>
                    <div className="font s14 b6 c68">Fan</div>
                  </div>
                  <div className="right">
                    <button
                      className={`fan-btn cleanbtn ${fan ? "on" : "off"}`}
                      onClick={() => setFan(!fan)}
                    >
                      <div className="txt font s13">ON</div>
                    </button>
                  </div>
                </div>
              </div>

              {/* Status of containers */}
              <div className="block containers">
                <div className="tit font s16 c68 upc">Status of containers</div>
                <div className="row flex aic">
                  <div className="meta flex aic">
                    <div className="ico">
                      <SolutionLevelIcon />
                    </div>
                    <div className="font s14 b6 c68">Solution level</div>
                  </div>
                  <div className="right">
                    <div className="val font s13 b6 c68">{drawerwaterlevel ? drawerwaterlevel : 'Loading'}%</div>
                  </div>
                </div>
                <div className="row flex aic">
                  <div className="meta flex aic">
                    <div className="ico pump">
                      <PumpStatusIcon />
                    </div>
                    <div className="font s14 b6 c68">Pump status</div>
                  </div>
                  <div className="right">
                    <button className={`cleanbtn status-btn flex aic active`}>
                      <div className="dot" />
                      <div className="txt font s11 upc cfff">{pumpworkstatus ? pumpworkstatus : 'Loading'}</div>
                    </button>
                  </div>
                </div>
              </div>

              {/* Status of drawer */}
              <div className="block containers">
                <div className="tit font s16 c68 upc">Status of drawer</div>
                <div className="row flex aic">
                  <div className="meta flex aic">
                    <div className="ico waterlevel">
                      <WaterLevelIcon />
                    </div>
                    <div className="font s14 b6 c68">Water Level</div>
                  </div>
                  <div className="right">
                    <div className="val font s13 b6 c68">{drawerwaterlevel ? drawerwaterlevel : 'Loading'}%</div>
                  </div>
                </div>
                <div className="row flex aic">
                  <div className="meta flex aic">
                    <div className="ico pump">
                      <ActivityStatusIcon />
                    </div>
                    <div className="font s14 b6 c68">Activity Status</div>
                  </div>
                  <div className="right">
                    <button className={`cleanbtn status-btn flex aic closed`}>
                      <div className="dot" />
                      <div className="txt font s11 upc cfff">{draweractivitystatus ? draweractivitystatus : 'Loading'}</div>
                    </button>
                  </div>
                </div>
              </div>

              {/* Status of drawer */}
              <div className="block containers">
                <div className="tit font s16 c68 upc">Status of cups</div>
                <div className="row flex aic">
                  <div className="meta flex aic">
                    <div className="ico waterlevel">
                      <WaterLevelIcon />
                    </div>
                    <div className="font s14 b6 c68">Water Level</div>
                  </div>
                  <div className="right">
                    <div className="val font s13 b6 c68">{cupswaterlevel ? cupswaterlevel : 'Loading'}%</div>
                  </div>
                </div>
                <div className="row flex aic">
                  <div className="meta flex aic">
                    <div className="ico pump">
                      <ActivityStatusIcon />
                    </div>
                    <div className="font s14 b6 c68">Activity Status</div>
                  </div>
                  <div className="right">
                    <button className={`cleanbtn status-btn flex aic closed`}>
                      <div className="dot" />
                      <div className="txt font s11 upc cfff">{cupactivitystatus ? cupactivitystatus : "Loading"}</div>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </Rightbar>
        </div>
      </div>
    </>
  );
};

export default Statistics;
